import React from "react"
import { HUtils } from "@macashipo/mlib"
import { kfnGetButtonList } from "src/helpers/fnList"
export default function MUIATHeaderButtons(props) {
  const _buttonList =
    HUtils.runFuntion(props.cfp.fnList, kfnGetButtonList, []) || []
  // console.warn("render header buttons", props, _buttonList)
  return (
    <div>
      {_buttonList &&
        _buttonList.length > 0 &&
        _buttonList.map((v, i) => {
          return v.renderMyButton({
            key: i,
            fnList: props.cfp.fnList,
          })
        })}
      {props.lastBtns != null && props.lastBtns}
    </div>
  )
}
